import React from 'react'
import { Grid, TextField, InputAdornment, IconButton } from '@material-ui/core'
import LoadingButton from 'Components/LoadingButton'
import commitRelay from 'Components/CommitRelay'
import graphql from 'babel-plugin-relay/macro'
import Copy from '@material-ui/icons/FileCopy'
import {snackbarSuccess} from 'Store/actions/snackbar'
import {makeStyles} from '@material-ui/core/styles'

const oauthMutation = graphql`
  mutation OAuthIndexMutation($username: String!) {
    generateOAuth(username: $username) {
      oAuth{
        OAuthId
      }
      message
    }
  }
`

const styles = makeStyles(theme => ({
  root: {
    padding: theme.spacing(1),
    width: "100%",
    margin: 0
  },
  text: {
    marginTop: theme.spacing(1)
  }
}))

function OAuthIndex(props) {
  let [username, setUsername] = React.useState("")
  let [requesting, setRequesting] = React.useState(false)
  let [token, setToken] = React.useState("")
  const classes = styles()

  function getToken() {
    setRequesting(true)
    commitRelay({username}, oauthMutation, (response, error) => {
      setRequesting(false)
      if(!error){
        setToken("https://www.gamingstars.in/account/auth/oauth/"+response.generateOAuth.oAuth.OAuthId)
      }
    })
  }

  function shareLink(type){
    if(type === "COPY") {
      let a = document.getElementById("token")
      if(!a){
        return
      }
      a.select()
      a.setSelectionRange(0, 9999)
      document.execCommand("copy")
      snackbarSuccess("Auth token link has been copied", Date.now())
    }
  }

  return <Grid container spacing={1} alignItems={'center'} className={classes.root}>
    <Grid item xs={12} sm={9} md={10}>
      <TextField value={username} label={"Email or phone"} onChange={e => setUsername(e.target.value)} fullWidth/>
    </Grid>
    <Grid item xs={12} sm={3} md={2}>
      <LoadingButton isLoading={requesting} onClick={getToken}>Get Token</LoadingButton>
    </Grid>
    <TextField value={token} id="token" label={"Auth token"} className={classes.text} fullWidth InputProps={{
      endAdornment: <InputAdornment position="end">
        <IconButton onClick={() => shareLink("COPY")}>
          <Copy />
        </IconButton>
      </InputAdornment>
    }}/>

  </Grid>
}

export default OAuthIndex