import React from 'react'
import { Switch, Route } from 'react-router-dom'
import CorousalContent from './Corousal/CorousalContent'
import Editor from './Editor/AddPage'

function Pages(props) {
  return <Switch>
    <Route path={'/pages/corousal'} component={CorousalContent} />
    <Route path={'/pages/terms'} component={Editor} />
  </Switch>
}

export default Pages