import React from 'react'
import {Switch, Route} from 'react-router-dom'
import InitiateTransaction from './Transactions/InitiateTransaction'
import OAuth from './OAuth/OAuthIndex'
import Users from './Users/UsersList'
import UserDetails from './Users/MoreDetailsUser'

function UserRoute() {
  return <Switch>
    <Route path={'/management/user/transactions'} component={InitiateTransaction} />
    <Route path={'/management/user/oauth'} component={OAuth} />
    <Route path={'/management/user/view/:username'} component={UserDetails} />
    <Route path={'/management/user/view'} component={Users} />
  </Switch>
}

export default UserRoute