import React from 'react'
import MaterialTable from 'Components/MaterialTable'
import makeDate from 'Utils/MakeDate'

function getWithdraws(withdraws = []) {
  return withdraws.map(item => ({
    ...item,
    createdAt: makeDate(item.createdAt)
  }))
}
/*method
          approved
          amount
          message
          createdAt*/
function ViewWithdraw(props) {
  const items = getWithdraws(props.withdraws)
  // console.log(items)
  return <MaterialTable tableData={items} title={"Withdrawals"} columns={[
    {title: "Method", field: "method", width: 32},
    {title: "Message", field: "message"},
    {title: "Amount", type: "currency", field: "amount", width: 32},
    {title: "Code", field: "approved", width: 32},
    {title: "Created At", field: "createdAt"}
  ]} getData={(data) => {console.log(data)}}
    editable={false}
  />
}

export default ViewWithdraw