import React from 'react'
import graphql from 'babel-plugin-relay/macro'
import {createRefetchContainer} from 'react-relay'
import Pagination from '@material-ui/lab/Pagination'
import { snackbarError } from 'Store/actions/snackbar'
import {Paper, Box, TablePagination, IconButton} from '@material-ui/core'
import Table from 'Components/Table'
import Loading from 'Components/Loading'
import {makeStyles} from '@material-ui/core/styles'
import ArrowForward from '@material-ui/icons/ArrowForward'
import { useHistory } from 'react-router-dom'

const styles = makeStyles(theme => ({
  paper: {
    position: "relative",
    overflow: "auto",
    maxHeight: `calc(100vh - 128px)`
  },
  loadingDiv: {
    height: "100%", 
    width: "100%", 
    zindex: 1, 
    background: "rgba(0, 0, 0, 0.6)", 
    display: "flex", 
    position: "absolute", 
    alignItems: "center", 
    justifyContent: "center"
  },
  paginationHolder: {
    display: "flex",
    width: "100%",
    justifyContent: "space-between",
    alignItems: "center"
  }
}))

function UsersTable({ classes, ...props}) {

  //const classes = styles()
  const history = useHistory()

  let body = props.users.map((item, index) =>([
    (index + 1) + ((props.page - 1) * props.limit),
    item.username,
    item.phone,
    item.walletBalance, 
    item.bonus,
    item.registeredTillToday,
    <IconButton onClick={e => history.push(`/management/user/view/${item.username}`)}><ArrowForward /></IconButton>
  ]))

  return <Paper className={classes.paper}>
    {props.fetching && <div className={classes.loadingDiv}><Loading isLoading={true}/></div>}
    <Table heading={['#', 'Username', 'Phone', 'Wallet', 'Bonus', "Matches", "More"]} body={body} caption={'Users list'}/>
  </Paper>
  
}

function RefetchUsers(props) {
  let [limit, setLimit] = React.useState(20)
  let [fetching, setFetching] = React.useState(false)
  const classes = styles()
  function refetchPage(page = 1, limit = 15) {
    setFetching(true)
    props.relay.refetch({page, limit}, null, err => {
      setFetching(false)
      if(err) {
        snackbarError(`Unable to fetch ${err}`)
      }
    })
    // props.setPage(page)
  }
  let usersList = {}
  try {
    usersList = props.users.usersList
  }catch(err) {
    console.error(err)
    usersList = {}
  }
  return <Box p={1}>
      <UsersTable classes={classes} users={usersList.docs} page={usersList.page} limit={limit} fetching={fetching}/>
      <div className={classes.paginationHolder}>
        <Pagination count={usersList.totalPages} variant="outlined" onChange={(e, page) => refetchPage(page, limit)} page={usersList.page} color="secondary"/>
        <TablePagination
            rowsPerPageOptions={[5, 10, 20, 50]}
            component="div"
            count={usersList.totalDocs}
            rowsPerPage={limit}
            page={usersList.page - 1}
            onChangePage={(e, page) => refetchPage(page + 1, limit)}
            onChangeRowsPerPage={e => { setLimit(e.target.value); refetchPage(usersList.page, e.target.value)}}
          />
        </div>
    </Box>
}

export default createRefetchContainer(RefetchUsers, {
  users: graphql`
    fragment RefetchUsers_users on Query 
    @argumentDefinitions(
      page: {type: "Int", defaultValue: 1}
      limit: {type: "Int", defaultValue: 15}
    ) {
      usersList(pagination: {page: $page, limit: $limit}){
        docs {
          username
          phone
          walletBalance
          bonus
          registeredTillToday
        }
        page
        totalPages
        totalDocs
      }
    }
  `
}, graphql`
  query RefetchUsersQuery($page: Int!, $limit: Int!) {
    ...RefetchUsers_users @arguments(page: $page, limit: $limit)
  }
`)