import React from 'react'
import { useForm } from "react-hook-form";
import { yupResolver } from '@hookform/resolvers';
import LoadingButton from 'Components/LoadingButton'
import * as yup from 'yup'
import Input from 'Components/FormComps/InputReactHook'
import Form from 'Components/Styled/FormRegular'
import graphql from 'babel-plugin-relay/macro'
import CommitRelay from 'Components/CommitRelay'
import { snackbarSuccess, snackbarError } from 'Store/actions/snackbar';

const schema = yup.object().shape({
  pickRoundTitle: yup.string().required(),
  pushInRoundTitle: yup.string().required(),
  splitRatio: yup.number().required().positive()
});

const movePlayersMutation = graphql`
  mutation DistributePlayersFromRoundMutation($data: MovePlayersToNextRoundInput!){
    movePlayersToNextRound(data: $data) {
      message
    }
  }
`

function DistributePlayersFromRound(props) {

  let [requesting, setRequesting] = React.useState(false)

  const {register, errors, handleSubmit} = useForm({
    resolver: yupResolver(schema)
  })

  function onSubmit(data) {
    if(!window.confirm("DO you wish to proceed?")) {
      return
    }
    if(data.pushInRoundTitle === data.pickRoundTitle) {
      return snackbarError("You can't pick and push in same round.")
    }
    setRequesting(true)
    CommitRelay({data}, movePlayersMutation, (response, error) => {
      setRequesting(false)
      if(!error) {
        snackbarSuccess(response.movePlayersToNextRound.message)
      }
    })
  }

  return <div>
    <Form component={"form"} onSubmit={handleSubmit(onSubmit)}>
      {/* <Input inputRef={register} name="topTeams" label="Top teams to pick" errors={errors} /> */}
      <input type="hidden" ref={register} value={props.megladronId} name="megladronId"/>
      <Input inputRef={register} value={props.round.primaryText} disabled={true} name="pushInRoundTitle" label="Round in which players will be pushed" errors={errors} />
      <Input inputRef={register} name="pickRoundTitle" label={"Round title from where to get players"} errors={errors} />
      <Input inputRef={register} name="splitRatio" label={"Number of matches a user plays"} errors={errors} />
      <LoadingButton type="submit" isLoading={requesting}>Seperate Players</LoadingButton>
    </Form>
  </div>
}

export default DistributePlayersFromRound;