import React from 'react'
import MaterialTable from 'Components/MaterialTable'
import makeDate from 'Utils/MakeDate'

function getTransactions(transactions = []) {
  return transactions.map(item => ({
    ...item,
    balance: `B: ${item.balanceUsed.bonus}, W: Rs.${item.balanceUsed.wallet}`,
    createdAt: makeDate(item.createdAt)
  }))
}
/*amount
          responseCode
          bankName
          status
          responseMessage*/
function ViewTransaction(props) {
  const items = getTransactions(props.transactions)
  // console.log(items)
  return <MaterialTable tableData={items} title={"Transaction"} columns={[
    {title: "Type", field: "transactionType", width: 32},
    {title: "Message", field: "responseMessage"},
    {title: "Amount", type: "currency", field: "amount", width: 32},
    {title: "Code", field: "responseCode", width: 32},
    {title: "Status", field: "status"},
    
    {title: "Balance Used", field: "balance"},
    {title: "Created At", field: "createdAt"}
  ]} getData={(data) => {console.log(data)}}
    editable={false}
  />
}

export default ViewTransaction