import React from 'react'
import CustomList from 'Components/CustomList'
import { useHistory } from 'react-router-dom'

function getTournaments(tournaments = []) {
  return tournaments.map(item => ({
    id: item._id,
    primaryText: item.title,
    secondaryText: `Prize Pool: Rs.${item.prizePool}, Entry: Rs.${item.entryFee}, Total Played: ${item.totalRegistered}`
  }))
}

function ViewTournaments(props) {
  const items = getTournaments(props.tournaments)
  // console.log(items, props)
  const history = useHistory()
  function itemClick(item = {}) {
    if(!item.id) {
      return
    }
    history.push(`/tournaments/${item.id}`)
  }

  return <CustomList items={items} heading={"Tournament details"} button itemClick={itemClick}/>
}

export default ViewTournaments